<template>
  <V-Scroll :isList="false" ref="categoryScroll" :scroll-height="`${mainHeight}px`">
    <div class="editGoodsCategory-box">
      <div class="tip">
        请勿超过分享物品的市场价，不会写价格？选择竞拍
      </div>
      <div class="tabs-box">
        <div class="tab-item">
          <div class="box">
            <span @click="handleTab(1)" :class="['item', tabType === 1 ? 'active' : null]">一口价</span>
            <span @click="handleTab(2)" :class="['item', tabType === 2 ? 'active' : null]">竞拍</span>
          </div>
        </div>
        <div class="tab-content">
          <div class="fixedPrice">
            <div class="option-block">
              <div class="item" v-if="tabType === 1">
                <span>一口价</span>
                <div class="contains">
                    <input type="number" class="shareInput" v-model.number.trim="bidPrice">
                </div>
                <span>个</span>
              </div>
              <template v-else>
                <div class="item">
                  <span>起拍价</span>
                  <div class="contains">
                    <input type="number" class="shareInput" v-model.number.trim="bidPrice">
                  </div>
                  <span>个</span>
                </div>
                <div class="item">
                  <span>竞拍时间</span>
                  <div class="contains">
                    <div class="slider">
                      <VSlider :slider-style="{backgroundColor: '#fff'}" :process-style="{backgroundColor: '#de2245'}" :tooltip="false"  :min="1" :max="24" v-model="auctionTime"></VSlider>
                    </div>
                  </div>
                  <span class="text">{{auctionTime}}小时</span>
                </div>
              </template>
              <div class="item">
                <span>分类标签</span>
                <span class="icon-arrow-right"></span>
              </div>
              <div class="item">
                <span>仅限同城享友</span>
                <VSwitch open-name="" close-name="" color="green" v-model="onlyCityFriends"></VSwitch>
              </div>
            </div>
            <div class="option-block">
              <div class="item" @click="handleDelivery">
                <span>普通快递</span>
                <span :class="['icon-checkbox', delivery ? 'active' : null]"></span>
              </div>
              <div class="item" @click="handleDoorToTake">
                <span>顺丰快递上门取件</span>
                <span :class="['icon-checkbox', doorToTake ? 'active' : null]"></span>
              </div>
            </div>
            <span class="edit-tip tip">快递费由享友支付给平台。</span>
            <div :class="['option-block', !isAnOptions ? 'first-item' : null]">
              <div class="item" @click="handleAnOptions">
                <span>高级选项</span>
                <span :class="['icon-arrow-down', isAnOptions ? 'arrow-up' : null]"></span>
              </div>
              <template v-if="isAnOptions">
                <div class="item">
                  <span>上架数量</span>
                  <div class="contains">
                    <input type="number" class="shareInput" v-model.number.trim="bidPrice">
                  </div>
                </div>
                <div class="item">
                  <span>仅限分享过的享友领取</span>
                  <VSwitch open-name="" close-name="" color="orange" v-model="onlyShareFriends"></VSwitch>
                </div>
                <div class="item">
                  <span>仅限我关注的人可以私信给我</span>
                  <VSwitch open-name="" close-name="" color="orange" v-model="onlyFocusFriends"></VSwitch>
                </div>
              </template>
            </div>
            <template v-if="isAnOptions">
              <span class="edit-tip tip">想送给制定好友？选择商家时间，生成海报后，发送给好友、社群或微信朋友圈。若超时未被领取，将自动商家。</span>
              <div class="option-block last-block">
                <div class="item">
                  <span>延迟上架</span>
                  <div class="contains-text">
                    <span class="text">立即上架</span>
                    <span class="icon-arrow-right"></span>
                  </div>
                </div>
              </div>
            </template>
            <div class="next-step">
              <button type="button" @click="handleNextStep" class="btn btn-next-step">确认发布</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </V-Scroll>
</template>

<script>
import VSwitch from "vue-switch/switch-2";
import VSlider from "vue-slider-component";
import Ajax from "@/mixins/ajax";

export default {
  name: "editGoodsCategory",
  mixins: [Ajax],
  components: {
    VSwitch,
    VSlider
  },
  data() {
    return {
      // tab切换 1: 一口价 2: 竞拍
      tabType: 1,
      // 是否仅限同城享友
      onlyCityFriends: false,
      // 仅限分享过的享友
      onlyShareFriends: false,
      // 仅限我关注的人可以私信给我
      onlyFocusFriends: false,
      // 普通快递
      delivery: false,
      // 上门取件
      doorToTake: false,
      // 是否显示高级选项
      isAnOptions: false,
      // 竞拍时间
      auctionTime: 1,
      // 起拍价
      bidPrice: 1
    };
  },
  methods: {
    /**
     * 处理普通快递选择
     */
    handleDelivery() {
      this.delivery = !this.delivery;
    },
    /**
     * 处理上门取件
     */
    handleDoorToTake() {
      this.doorToTake = !this.doorToTake;
    },
    /**
     * 处理展开选项
     */
    handleAnOptions() {
      this.isAnOptions = !this.isAnOptions;
      this.$nextTick(() => {
        this.$refs.categoryScroll.refresh();
      });
    },
    handleTab(type) {
      this.tabType = type;
      this.$nextTick(() => {
        this.$refs.categoryScroll.refresh();
      });
    },
    handleNextStep() {
      this.$router.replace({ name: "ReleaseWin" });
    }
  }
};
</script>

<style scoped>
.tip {
  height: 1.2rem;
  line-height: 1.2rem;
  text-align: center;
  background: #ffe7d0;
  font-size: 0.39rem;
  color: #fa7d2a;
}
.edit-tip {
  align-self: flex-start;
  background: transparent;
  color: #888;
  height: auto;
  line-height: normal;
  padding: 0.19rem 0.45rem;
  margin-bottom: 0.58rem;
}
.tabs-box .tab-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0.45rem 0.52rem;
  background: #fff;
}
.tabs-box .tab-item .box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 2px solid #de2245;
  font-size: 0.45rem;
}
.tabs-box .tab-item .box .item {
  width: 5.1rem;
  height: 0.99rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #de2245;
}
.tabs-box .tab-item .box .item.active {
  background: #de2245;
  color: #fff;
}

.fixedPrice {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.option-block {
  display: flex;
  flex-direction: column;
  margin-top: 0.3rem;
  background: #fff;
  font-size: 0.42rem;
}
.option-block .item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.53rem 0.45rem;
  border-bottom: 1px solid #ddd;
}
.option-block .item:last-child {
  border-bottom: none;
}
.contains {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-left: 0.2rem;
}
.contains .slider {
  flex-grow: 1;
}
.option-block .text {
  white-space: nowrap;
  margin-left: 0.5rem;
}
.option-block .contains-text {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.option-block .contains-text .text {
  margin-right: 0.1rem;
  margin-top: -0.03rem;
}
.first-item,
.last-block {
  margin-bottom: 1.04rem;
}

.next-step {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0.25rem 0.45rem;
  background: #fff;
}
.next-step .btn {
  width: 10.35rem;
  height: 1.32rem;
  border-radius: 0.66rem;
  color: #fff;
  font-size: 0.54rem;
  text-align: center;
}
.next-step .btn.btn-next-step {
  background-color: #de2245;
}
.shareInput {
  width: 100%;
  text-align: right;
  margin-right: 0.1rem;
  font-size: 0.42rem;
  border: none;
}

.icon-arrow-down {
  width: 0.42rem;
  height: 0.23rem;
  background: url("../../images/icon-arrow-down.png") no-repeat;
  background-size: 100%;
  transition: all 0.3s ease-in;
}
.icon-arrow-down.arrow-up {
  transform: rotate(-180deg);
}
.icon-arrow-right {
  width: 0.23rem;
  height: 0.42rem;
  background: url("../../images/icon-arrow-right.png") no-repeat;
  background-size: 100%;
}
.icon-checkbox {
  width: 0.69rem;
  height: 0.69rem;
  background-image: url("../../images/icon-checkbox.png");
  background-repeat: no-repeat;
  background-size: 100%;
}
.icon-checkbox.active {
  background-image: url("../../images/icon-checkbox-active.png");
}
</style>



